import './FuncExample.css'
import React , {useState , useEffect } from 'react'; 

export const FuncExample = () => { 

    const [items , setItems] = useState([]); 
    const [show ,setShow] = useState (false); 

       
     useEffect (()=> {
            fetch('https://jsonplaceholder.typicode.com/posts/1')
            .then (res => res.json())
            .then(
                (result)=> {
                    setItems(result);
                }  
                            )
        } ) 
    

     

    if(show) { 
        return(
            <div className="conteiner">               
                    <div className="comment" key={items.id}>
                        <h3>Описание: {items.title}</h3>
                        <p>Пост: {items.body}</p>
             <button onClick={()=>{setShow(true)}}>Показать</button>

                    </div>
            </div>
        )
    } else {

   return(
       <div className="conteiner">
           <button onClick={()=>{setShow(true)}} className="btn">Показать</button>
       </div>
   )

  }
}